import React from 'react'
import section from './section1.png';
import Section from '../Section'


const questions = [
  {
    id: 1,
    q: '()'
  },
  {
    id: 2,
    q: ''
  },
  {
    id: 3,
    q: ''
  },
]

const index = () => {
  return (
    <Section
      questions={questions}
      level=""
      order=""
      subtitle=""
      section={section}
      next=""
      to="/section2"
      type="button"
    />
  )
}

export default index
